@font-face {
  font-family: "Consolas";
  src: url("../resources/fonts/Consolas-Regular.woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Consolas";
  src: url("../resources/fonts/Consolas-Bold.woff2");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Helvetica";
  src: url("../resources/fonts/Helvetica-Regular.woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../resources/fonts/OpenSans-Italic.woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: Arial;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: #0035FF;
  transition: color 0.15s ease-in-out;
}

h1 {
  margin: 0;
  padding: 0;
}

h2 {
  margin: 0;
  padding: 0;
}

h3 {
  margin: 0;
  padding: 0;
}

h4 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

figure {
  margin: 0;
  padding: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-container {
  width: 100%;
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}
.input-reset::-webkit-outer-spin-button, .input-reset::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  transition: all 0.15s ease-in-out;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  margin: 0 auto;
  padding: 0 143px;
  width: 100%;
  max-width: 1024px;
}

@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}
.hero {
  margin-bottom: 55px;
  padding-top: 87px;
}
.hero__title {
  padding: 0 30px;
  font-family: "Consolas", sans-serif;
  text-align: center;
  font-size: 48px;
  font-weight: 700;
  line-height: 48px;
  background-color: #FFFFFF;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  color: #0062FF;
  z-index: 10;
}
.hero__banner {
  border-bottom: 0.5px solid #DCDCDC;
}

.title {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 69px;
}
.title:before {
  content: "";
  position: absolute;
  top: 45%;
  left: 0;
  height: 1px;
  width: 100%;
  border-top: 0.5px solid #DBDBDB;
}

.img-books-1 {
  margin-bottom: 16px;
}

.img-descr {
  margin-bottom: 50px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-style: italic;
  color: #202326;
}

@media (max-width: 767px) {
  .hero {
    margin-bottom: 25px;
    padding-top: 40px;
  }
  .hero__title {
    padding: 0 15px;
    font-size: 23px;
    line-height: 25px;
  }
  .hero__banner {
    display: flex;
    flex-direction: column-reverse;
    border-bottom: none;
  }
  .hero .title {
    margin-bottom: 40px;
  }
  .hero .img-descr {
    margin-bottom: 26px;
  }
  .hero .img-books-1 {
    max-width: unset;
  }
  .hero .scroll-img {
    overflow-x: scroll;
    overflow-y: hidden;
  }
  .hero .scroll-img {
    scrollbar-width: thin;
    scrollbar-color: #0062FF #f9f9fd;
  }
  .hero .scroll-img::-webkit-scrollbar {
    height: 5px;
    background-color: #f9f9fd;
  }
  .hero .scroll-img::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #0062FF;
  }
  .hero .scroll-img::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #E6E6E6;
    box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
  }
}
.quote {
  margin-bottom: 45px;
}
.quote__title {
  font-family: "Consolas", sans-serif;
  font-size: 28px;
  line-height: 36px;
}
.quote__description {
  padding: 30px 0 0 58px;
  font-family: "OpenSans", sans-serif;
  font-size: 18px;
  line-height: 24px;
  color: #1A1A1A;
  background: url("../img/quote.svg") no-repeat left top;
  background-position-y: 21px;
}
.quote__author {
  display: block;
  margin-top: 27px;
  font-size: 16px;
  line-height: 24px;
  color: #777777;
}

@media (max-width: 767px) {
  .quote {
    margin-bottom: 54px;
  }
  .quote__title {
    font-size: 23px;
    line-height: 36px;
  }
  .quote__description {
    padding: 21px 0 0 53px;
    font-size: 14px;
    line-height: 20px;
  }
  .quote__author {
    margin-top: 20px;
    font-size: 14px;
  }
}
.advertisement {
  margin-bottom: 38px;
  padding-top: 34px;
  padding-bottom: 36px;
  max-width: 100%;
  background-color: #EDEDED;
}
.advertisement__dscr {
  display: block;
  margin-bottom: 17px;
  font-family: "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
}
.advertisement__link {
  text-decoration: underline;
}
.advertisement__link:hover {
  color: #001b7c;
  transition: color 0.15s ease-in-out;
}
.advertisement__cont {
  padding-left: 64px;
}

@media (max-width: 767px) {
  .advertisement {
    margin-bottom: 23px;
    padding-top: 25px;
    padding-bottom: 16px;
  }
  .advertisement__cont {
    padding-left: 0;
  }
  .advertisement__dscr {
    margin-bottom: 10px;
  }
}
.ending {
  margin-bottom: 74px;
}
.ending__title {
  margin-bottom: 29px;
  font-family: "Consolas", sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 36px;
}
.ending__description {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

@media (max-width: 767px) {
  .ending {
    margin-bottom: 30px;
  }
  .ending__title {
    margin-bottom: 24px;
    font-size: 21px;
    line-height: 36px;
  }
  .ending__description {
    font-size: 14px;
    line-height: 18px;
  }
}
.feedback {
  margin-bottom: 60px;
  padding: 40px 0;
  background-color: #EDEDED;
}

.rating {
  margin-bottom: 25px;
}
.rating__title {
  margin-bottom: 10px;
  font-family: "Consolas", sans-serif;
  font-size: 18px;
  line-height: 18px;
}

.star {
  display: block;
  width: 25px;
  height: 25px;
  fill: inherit;
  stroke: inherit;
  transition: all 0.15s ease-in-out;
}

.rating-group {
  display: inline-flex;
}

.rating-input {
  position: absolute;
  left: -9999px;
}

.rating-label {
  margin-right: 10px;
  fill: #FFB400;
  stroke: #FFB400;
  cursor: pointer;
}

.rating-input:checked ~ .rating-label .star {
  fill: transparent;
  stroke: #979797;
}

.rating-group:hover .rating-label .star {
  fill: #FFB400;
  stroke: #FFB400;
}

.rating-group .rating-input:hover ~ .rating-label .star {
  fill: transparent;
  stroke: #979797;
}

.comments__title {
  margin-bottom: 15px;
  font-family: "Consolas", sans-serif;
  font-size: 18px;
  line-height: 18px;
}
.comments__inputs {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.label {
  position: relative;
  display: block;
}
.label:not(:last-child) {
  margin-right: 30px;
}

.input {
  padding: 15px 20px;
  min-height: 50px;
  height: 100%;
  background-color: #ffffff;
  border: 1px solid #E6E6E6;
  border-radius: 6px;
  outline: none;
  transition: border-color 0.15s ease-in-out, background-color 0.15s ease-in-out;
}
.input::placeholder {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #777777;
}
.input:hover {
  border-color: #C3C2BEFF;
  transition: border-color 0.15s ease-in-out;
}
.input:focus {
  border-color: #C3C2BEFF;
  transition: all 0.15s ease-in-out;
}

.input-anim:focus {
  padding-bottom: 4px;
  transition: all 0.15s ease-in-out;
}
.input-anim:not(:placeholder-shown) {
  padding-bottom: 4px;
  transition: all 0.15s ease-in-out;
}

.label-name {
  position: absolute;
  top: 18px;
  left: 20px;
  font-size: 14px;
  line-height: 14px;
  transition: all 0.15s ease-in-out;
}

.input-anim:focus + .label-name, .input-anim:not(:placeholder-shown) + .label-name {
  top: 8px;
  font-size: 11px;
  color: #777777;
  transition: all 0.15s ease-in-out;
}

.input-feedback {
  margin-bottom: 15px;
  min-height: 120px;
  max-height: none;
}

.choices {
  width: 100%;
  max-width: 226px;
}
.choices__inner {
  display: inline-block;
  padding: 15px 25px 15px 15px;
  vertical-align: center;
  min-height: 50px;
  background-color: #FFFFFF;
  border: 2px solid #E6E6E6;
  border-radius: 6px;
  overflow: hidden;
}
.choices__item--choice::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 0;
  width: 7px;
  background-color: #0062FF;
}
.choices__item--choice:hover::before {
  height: 100%;
  transition: all 0.2s ease-in-out;
}

.choices__list--single .choices__item {
  font-size: 14px;
  line-height: 14px;
}

.choices[data-type*=select-one]:after {
  top: 48%;
  right: 22px;
  width: 7px;
  height: 7px;
  border-color: #000000;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
  transition: transform 0.5s;
}

.choices[data-type*=select-one].is-open:after {
  content: "";
  margin-top: 0;
  top: 45%;
  border-color: #000000;
  transform: rotate(225deg);
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 15px;
}

.is-open .choices__inner {
  border-radius: 6px;
}

.is-focused .choices__inner, .is-open .choices__inner {
  border-color: #7F7F7F;
}

.choices__placeholder {
  color: #777777;
  opacity: 1;
}

.choices__list--single {
  display: inline-block;
  padding: 0;
  width: 100%;
}

.is-open .choices__list--dropdown, .is-open .choices__list[aria-expanded] {
  border-color: transparent;
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding: 15px 25px;
}

.choices__item--choice.is-highlighted::before {
  height: 100%;
  transition: all 0.2s ease-in-out;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background-color: #EDEDED;
}

.choices__list--dropdown, .choices__list[aria-expanded] {
  position: absolute;
  top: 121%;
  margin-top: -1px;
  width: 100%;
  background-color: #FFFFFF;
  border: none;
  border-radius: 0;
  box-shadow: 0 2px 15px rgba(154, 154, 154, 0.5);
  word-break: break-all;
  will-change: visibility;
  visibility: hidden;
  overflow: hidden;
  z-index: 1;
}

#choices--book-select-item-choice-1 {
  display: none;
}

.btn-form {
  padding: 14px 70px;
  font-size: 14px;
  line-height: 16px;
  border-radius: 2px;
  background-color: #0062FF;
  color: #FFFFFF;
}
.btn-form:hover {
  background-color: #0156de;
}

.input--invalid {
  border-color: #FF5733FF;
  background-color: #FFE6E0FF;
}

.error {
  position: relative;
  top: 4px;
  font-size: 11px;
  line-height: 12px;
  color: #FF5733FF;
}

@media (max-width: 767px) {
  .feedback {
    margin-bottom: 35px;
  }

  .rating {
    margin-bottom: 15px;
  }
  .rating__title {
    margin-bottom: 15px;
  }

  .comments__title {
    margin-bottom: 10px;
    font-size: 16px;
    line-height: 18px;
  }
  .comments__inputs {
    margin-bottom: 10px;
    flex-direction: column;
  }

  .label {
    margin-bottom: 10px;
  }
  .label:not(:last-child) {
    margin-right: 0;
  }

  .error {
    top: -8px;
  }

  .choices {
    max-width: unset;
  }

  .input-feedback {
    margin-bottom: 6px;
  }

  .btn-form {
    width: 100%;
  }
}
.similar-posts {
  margin-bottom: 75px;
}
.similar-posts__title {
  margin-bottom: 40px;
  font-family: "Consolas", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 28px;
}

.posts {
  width: 100%;
  overflow: visible;
  overflow-x: clip;
}
.posts__image {
  display: block;
  margin-bottom: 20px;
  max-height: 226px;
  transition: transform 0.15s ease-in-out;
  overflow: hidden;
}
.posts__image:hover {
  transform: scale(1.03);
  transition: transform 0.15s ease-in-out;
}
.posts__link {
  display: inline-block;
  font-family: "Consolas", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  color: #272522;
}
.posts__link:hover {
  color: #0035FF;
  transition: color 0.15s ease-in-out;
}
.posts__pagination {
  opacity: 0;
}

@media (max-width: 767px) {
  .similar-posts {
    margin-bottom: 65px;
  }
  .similar-posts__title {
    margin-bottom: 25px;
    font-size: 23px;
    line-height: 28px;
  }

  .posts__image {
    margin-bottom: 13px;
    max-height: unset;
  }
  .posts__link {
    width: 84%;
  }
  .posts__pagination {
    position: relative;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
  }

  .swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
    bottom: -20px;
  }

  .post-img {
    width: 100%;
  }

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
  }

  .swiper-pagination-bullet-active {
    transform: scale(1.5);
    transition: transform 0.15s ease-in-out;
  }
}